import type { Essentials } from '@wix/fe-essentials/core';
import {
  ErrorHandler,
  ErrorHandlerParams,
} from '../error-handler/ErrorHandler';

export interface CreateErrorHandlerFromEssentialsParams
  extends Partial<ErrorHandlerParams> {}

export function createErrorHandlerFromEssentials(
  essentials: Essentials,
  params: CreateErrorHandlerFromEssentialsParams,
) {
  return new ErrorHandler({
    biLoggerOrFactory:
      essentials.asPartial.biLogger ?? essentials.biLoggerFactory,
    createErrorMonitor: (options) =>
      essentials.createChildEssentials({ errorMonitor: options }).errorMonitor,
    createExperiments: (options) =>
      essentials.createChildEssentials({ experiments: options }).experiments,
    environment: essentials.environment,
    createI18n: (options) =>
      essentials.createChildEssentials({ i18n: options }).i18n,
    ...params,
  });
}
